<template>
  <div class="content">
    <div class="sidebar">
      <div class="left-nav">
        <ul>
          <li>
            <img src="./images/user.png">
            <router-link to="/user/account">Личная страница</router-link>
          </li>
          <li>
            <img src="./images/user.png">
            <router-link to="/user/profile">Личные данные</router-link>
          </li>
          <li v-if="false">
            <img src="./images/user.png">
            <router-link to="/user/balance">Пополнить баланс</router-link>
          </li>
          <li>
            <img src="./images/settings.png">
            <router-link to="/user/settings">Настройки</router-link>
          </li>
          <li>
            <img src="./images/info.png">
            <router-link to="/user/faq">Вопросы и ответы</router-link>
          </li>
          <li>
            <img src="./images/info.png">
            <a href="#" @click="loggout">Выйти</a>
          </li>
        </ul>
      </div>
    </div>
    <div v-if="loading" class="main">
      <div class="loader"></div>
    </div>
    <div v-else class="main scrollbar">
      <router-view :user="user" @changeAvatar="changeAvatar" @changeName="changeName"></router-view>
    </div>
  </div>
</template>

<script>



export default {
  name: 'UserPage',
  data() {
    return {
      user: {
        name: '',
        lastname: '',
        birthday: '',
        phone: '',
        gender: '',
        avatar: '',
        address: '',
        postcode: '',
        nickname: '',
        email: '',
        country: '',
        region: '',
        city: '',
        balance: '',
      },
      token: localStorage.getItem('token'),
      loading: false,
    };
  },
  methods: {
    loggout() {
      localStorage.removeItem('token');
      this.$emit('logged');
      this.$router.push('/');
    },
    changeName(name) {
      this.$emit('setUserName', name);
    },
    changeAvatar(avatar) {
      const data = {
        name: 'A',
        avatar : avatar,
      };
      this.$emit('setUserNameforHeader', data);
    },
    fetchIt() {
      this.loading = true;
      this.$http.get(`/user/data?access_token=${this.token}`).then((r) => {
        this.user.name = r.data.name;
        this.user.lastname = r.data.lastname;
        this.user.birthday = r.data.birthday;
        this.user.phone = r.data.phone;
        this.user.gender = (r.data.gender === 'male') ? 'Мужской' : 'Женский';
        this.user.avatar = r.data.avatar;
        this.user.country = r.data.country;
        this.user.address = r.data.address;
        this.user.postcode = r.data.postcode;
        this.user.nickname = r.data.nickname;
        this.user.email = r.data.email;
        this.user.region = r.data.region;
        this.user.city = r.data.city;
        this.user.balance = r.data.balance;
        
        this.loading = false;

        this.$emit('setUserNameforHeader', this.user);

        }).catch(err => {
          console.log('Error ', err.response);
          localStorage.removeItem('token');
          this.$emit('logged');
          this.$router.push('/');
        });
    },
  },
  mounted() {
    this.fetchIt();
  },
};
</script>


