<template>
  <div class="center home">
    <div class="home__slogan">
      <h1 class="home__slogan-text">Eдиный аккаунт для всех сервисов otau</h1>
    </div>
    <div class="home__started">
      <img src="../assets/otau_logo.png" class="logo">
        <form v-if="isLogin === 'regStep1'" @submit.prevent="registerStep1" >
          <div v-if="!phoneFixPreload">
            <phone-input  class="inputRegisterPhone" :phone="phone"/><br/>
            <span v-if="validate.phone">Номер зарегистрирован!</span>
            <span v-if="validate.phoneFull">Введите полный номер</span>
             <span v-if="validate.phoneGet">Подождите 1 мин</span>
            <button>Далее</button><br />
            <li><a @click="toLogin(true)">У меня есть аккаунт</a></li>
          </div>
          <div v-else>
            <div class="loader"></div>
          </div>
        </form>
        <form v-else-if="isLogin === 'regStep2'" @submit.prevent="registerStep2" >
          <div v-if="!preloaderSmsSend">
            <img src="../assets/sms_code.png" class="b_sms-code">
            <input
              id="code"
              type="text"
              v-model="register.codeSMS"
              value="register.codeSMS"
              maxlength="5"
              placeholder="Код подтверждения"><br />
            <span v-if="validate.codeSms">Неправильный код-СМС</span>
            <button>Далее</button> <br />
            <li><a @click="toLogin(false)">Назад</a></li>
          </div>
          <div v-else>
            <div class="loader"></div>
          </div>
        </form>
        <form v-else-if="isLogin === 'regStep3'" @submit.prevent="registerStep3" >
          <ul v-if="!preloaderEndRegister">
          <li>
            <img src="../assets/name.png">
            <input
              type="text"
              v-model.trim="register.name"
              autocomplete="off"
              placeholder="Имя"
              @input="changeName"
              maxlength="36">
            <span class="wr br n-warning-of-name" v-if="msgValid.name">Заполните это поле.</span>
          </li>
          <li>
            <img src="../assets/sname.png">
            <input
              type="text"
              v-model.trim="register.lastname"
              autocomplete="off"
              placeholder="Фамилия"
              @input="changeLastName"
              maxlength="36">
            <span class="wr br" v-if="msgValid.lastname">Заполните это поле.</span>
          </li>
          <li>
            <div class="boxB">
              <img src="../assets/calendar.png">
            </div>
              <datepicker
                v-model="date.value"
                :disabledDates="date.disabledDates"
                :language="date.ru"
                input-class="inputDate"
                calendar-class="inputCalen"></datepicker>
            <span class="wr br" v-if="msgValid.birthday">Заполните это поле.</span>
          </li>
          <li class="mg-5">
            <img src="../assets/gender.png" class="gender_icon">
          <select class="gender__input" v-model="selected">
            <option class='n-gender' disabled value="">Пол</option>
            <option class='n-gender'>Мужской</option>
            <option class='n-gender'>Женский</option>
          </select>
            <span class="wr br" v-if="msgValid.gender">Выберите это поле</span>
          </li>
          <li>
            <img src="../assets/password.png">
            <input
              type="password"
              v-model="register.password"
              autocomplete="off"
              @input="changePass"
              placeholder="Пароль"
              maxlength="36"> <br />
            <span class="wr" v-if="msgValid.password">Введите пароль</span>
            <span class="wr" v-if="msgValid.minSimbol">Минимум 6-символов.</span>
          </li>
          <li>
            <img src="../assets/password.png">
            <input
              id="name"
              type="password"
              autocomplete="off"
              v-model="register.confirmPassword"
              @input="changePass2"
              placeholder="Подтверждение"
              maxlength="36"> <br />
            <span class="wr br" v-if="msgValid.confirmPassword">Повторите пароль.</span>
            <span class="wr br" v-if="msgValid.passAndpassConf">Пароли не совподают</span>
          </li>
          <li class='n-li-text-agreement'><p class='n-text-agreement'>Нажимая кнопку «Зарегистрироваться», <br>вы принимаете условия <br>Пользовательского соглашения</p></li>
          <li><button type="submit">Зарегистрироваться</button></li>
          </ul>
          <ul v-else>
            <div class="loader"></div>
          </ul>
        </form>
    </div>
  </div>
</template>

<script>
import Datepicker from 'vuejs-datepicker';
import { ru } from 'vuejs-datepicker/dist/locale';
import PhoneInput from './PhoneInput.vue';


export default {
  name: 'HomePage',
  components: {
    PhoneInput,
    Datepicker,
  },
  data() {
    return {
      isLogin: 'regStep1',
      phone: {
        code: '',
        number: '',
      },
      inviter_id: '',
      phoneFixPreload: false,
      preloaderSmsSend: false,
      preloaderEndRegister: false,
      msgValid: {
        name: false,
        lastname: false,
        password: false,
        birthday: false,
        gender: false,
        confirmPassword: false,
        minSimbol: false,
        passAndpassConf: false,
      },
      register: {
        codeSMS: '',
        password: '',
        confirmPassword: '',
        name: '',
        lastname: '',
        birthday: '',
        gender: '',
        token: '',
      },
      selected: '',
      date: {
        value: '1993-2-1',
        ru: ru,
        disabledDates: {
          to: new Date(1902, 1, 1), // Disable all dates up to specific date
          from: new Date(2003, 0, 0),
        },
      },
      loginValidate: {
        login: false,
      },
      validate: {
        phone: false,
        phoneFull: false,
        codeSms: false,
        phoneGet: false,
      },
    };
  },

  created() {
    const inviter = this.$route.query.inviter_id;
    if (inviter) {
      this.inviter_id = inviter;
    }
  },



  computed: {
    loginValidateChange() {
      return {
        borderBottom: this.loginValidate.login ? '1px solid var(--color-primary-notific)' : '',
        // boxShadow: this.loginValidate.login ? '0 0 10px rgba(0,0,0,0.5)' : '',
      };
    }
  },

  methods: {
    changeName(e) {
      this.msgValid.name = false;
    },
    changeLastName(e) {
      this.msgValid.lastname = false;
    },
    changePass(e) {
      this.msgValid.password = false;
      this.msgValid.minSimbol = false;
      this.msgValid.passAndpassConf = false;
    },
    changePass2(e) {
      this.msgValid.confirmPassword = false;
      this.msgValid.passAndpassConf = false;
    },
    toLogin(data) {
      if(data){
        this.$router.push('/');
      } 
      else{
        this.isLogin = 'regStep1';
      }
    },
    registration() {
      this.isLogin = 'regStep1';
      this.phone.number = '';
    },
    registerStep1() {

      this.phoneFixPreload = true;

      this.validate.phone =  false;
      this.validate.phoneFull = false;
      this.validate.phoneGet = false;

        this.$http.post('/signup', {
          phone: this.phone.number,
        })
        .then(res => {
          this.isLogin = 'regStep2';
          this.phoneFixPreload = false;
        })
        .catch(error => {
          this.phoneFixPreload = false;

          console.log('Ошибка: ', error.response.data.error);

          if (error.response.data.error === 'Phone exist') {
            this.validate.phone = true;
            return;
          }

          if (error.response.data.error === 'Send full phone number with +7') {
            this.validate.phoneFull = true;
            return;
          }


          if (error.response.data.error === 'To resend SMS, wait 1 min') {
            this.validate.phoneGet = true;
            return;
          }

        });
    },
    registerStep2() {

      this.preloaderSmsSend = true;

      this.validate.codeSms = false;
      let codeSMS = this.register.codeSMS.trim();
 
        this.$http.post('/signup/verify-phone', {
          phone: this.phone.number,
          code: this.register.codeSMS,
        })
        .then(res => {
          this.register.token = res.data.access_token;
          this.isLogin = 'regStep3';
          this.register.codeSMS = '';
          this.preloaderSmsSend = false;
        })
        .catch(error => {
          this.preloaderSmsSend = false;
          if (error.response.data.error === 'Code error') {
            this.validate.codeSms = true;
            return;
          }
        });
    },
    registerStep3() {

      let password = this.register.password.trim();
      let cPassword = this.register.confirmPassword.trim();

      this.msgValid.birthday = false;
      this.msgValid.gender = false;

      const config = {
        headers: { 'Authorization': `Bearer ${this.register.token}` }
      };

      if (this.register.name.length < 1) {
        this.msgValid.name = true;
        return;
      }
      
      if (this.register.lastname.length < 1) {
        this.msgValid.lastname = true;
        return;
      }

      // date
      let d = this.date.value;
      if (d === '1993-2-1') {
        this.msgValid.birthday = true;
        return;
      } else {
        let curr_date = d.getDate();
        let curr_month = d.getMonth() + 1;
        let curr_year = d.getFullYear();
        d = curr_year + "-" + curr_month + "-" + curr_date;
      }
      //----------- Date End ----------

      if (!this.selected) {
        this.msgValid.gender = true;
        return;
      }

      if (this.register.password.length < 1) {
        this.msgValid.password = true;
        return;
      }

      if (this.register.confirmPassword < 1) {
        this.msgValid.confirmPassword = true;
        return;
      }
      
      if (this.register.password !== this.register.confirmPassword) {
        this.msgValid.passAndpassConf = true;
        return;
      }

      if (password === cPassword && password.length >= 6) {
        let obj = {
          name: this.register.name,
          lastname: this.register.lastname,
          birthday: d,
          gender: (this.selected === 'Мужской') ? 'male' : 'female',
          password: this.register.password,
          inviter_id: this.inviter_id,
        };

        this.preloaderEndRegister = true;

        this.$http.post('/signup/data', obj, config)
          .then(res => {
            this.register.name = '';
            this.register.lastname = '';
            this.date.value = '1993-2-1';
            this.register.password = '';
            this.register.confirmPassword = '';
            this.inviter_id = '';

            this.preloaderEndRegister = false;

            this.$modal.show('dialog', {
            text: 'Вы успешно зарегистрировались!',
            buttons: [
              {
                title: 'Ок'
              }
            ]});

          setTimeout(()=>{
            this.$router.push('/');
          }, 3000);


          })
          .catch(error => {
            this.preloaderEndRegister = false;
            console.log('Ошибка ' + error.response.data.error);
            if (error.response.data.error === 'Phone exist') {
              this.$modal.show('dialog', {
                text: 'Телефон существует в базе',
                buttons: [
                  {
                    title: 'Закрыть'
                  }
              ]});
              this.register.name = '';
              this.register.lastname = '';
              this.date.value = '1993-2-1';
              this.register.password = '';
              this.register.confirmPassword = '';
            } else if (error.response.data.error === 'The password must consist of Latin letters, numbers and symbols') {
              this.$modal.show('dialog', {
                text: 'Введите пароль на латинице',
                buttons: [
                  {
                    title: 'Закрыть'
                  }
              ]});
              this.register.password = '';
              this.register.confirmPassword = '';
            }
          });

      } else {
        this.msgValid.minSimbol = true;
      }
    },

  },
};
</script>

