<template>
  <div class="center home">
    <div class="home__slogan">
      <h1 class="home__slogan-text">Eдиный аккаунт для всех сервисов <a class="home__slogan-text" href="https://otau.org">otau</a></h1>
    </div>
    <div class="home__started">
      <img src="../assets/otau_logo.png" class="logo">
        <form @submit.prevent="logIn" >
          <ul>
          <li>
            <img src="../assets/login.png">
            <input
              type="text"
              v-model="login"
              autocomplete="off"
              @input="loginPhoneChange"
              placeholder="Номер телефона"
              :style="loginValidateChange">
          </li>
          <li>
            <img src="../assets/password.png">
            <input
              type="password"
              v-model="password"
              placeholder="Пароль"
              autocomplete="off">
          </li>
          <li><button type="submit">Войти</button></li>
          <li><a @click="toRestorePassPage">Забыли пароль?</a></li>
          <li><a @click="registration">Регистрация</a></li>
          </ul>
        </form>
    </div>
  </div>
</template>

<script>


export default {
  name: 'HomePage',
  data() {
    return {
      login: '',
      password: '',
      phoneFixPreload: false,
      isValid: false,
      loginValidate: {
        login: false,
      },
      validate: {
        phone: false,
        phoneFull: false,
        codeSms: false,
        phoneGet: false,
      },
    };
  },

  computed: {
    loginValidateChange() {
      return {
        borderBottom: this.loginValidate.login ? '1px solid var(--color-primary-notific)' : '',
      };
    }
  },
  methods: {
    toLogin() {
      this.isLogin = 'login';
    },
    onInput(e) {
      if (isValid) {
        this.isValid = isValid;
      }
    },
    registration() {
      this.$router.push('/register');
    },

    toRestorePassPage() {
      this.$router.push('/restore-password');
    },


    loginPhoneChange(){

      this.loginValidate.login = false;
      this.$modal.hide('dialog');

      if (this.login[0] !== '+') {
        this.login = '+' + this.login;
      }

      if (!Number(this.login.slice(1))) {
        const first = Number(this.login.slice(1));
        if (first === 0) {
          return;
        }

        this.loginValidate.login = true;

        this.$modal.show('dialog', {
          text: 'Введите номер телефона!',
          buttons: [{ title: 'Закрыть' }],
        });
      }
    },
    logIn() {
      this.$http.post('/login', {
        phone: this.login,
        password: this.password,
      }).then((res) => {
        const token = res.data.access_token;
        if (token) {
          localStorage.setItem('token', token);
          this.$emit('logged');
          this.$router.push('/user');
        }
      }).catch(() => {
        this.$modal.show('dialog', {
          text: 'Ошибка логина или пароля!',
          buttons: [{ title: 'Закрыть' }],
        });
      });
    },
  },
};
</script>


