<template>
  <div class="container">
    <H3>Изменить персональные данные</H3>
    <form @submit.prevent="submit">
      <div class="form-group">
        <label for="old_password">Текущий пароль</label>
        <input id="old_password"
          type="password"
          autocomplete="off"
          v-model="password_old"
          maxlength="36"
          minlength="6">
      </div>
      <div class="form-group">
        <label for="password">Новый пароль</label>
        <input id="password"
          type="password"
          autocomplete="off"
          v-model="password"
          maxlength="36"
          minlength="6">
      </div>
      <div class="form-group">
        <label for="password_c">Подтверждение</label>
        <input id="password_c"
          type="password"
          autocomplete="off"
          v-model="password_c"
          maxlength="36"
          minlength="6">
      </div>
      <button type="submit" :disabled="onSubmit">{{ save }}</button>
    </form>
  </div>
</template>

<script>
export default {
  name: 'Settings',
  data() {
    return {
      password_old: '',
      password: '',
      password_c: '',
      onSubmit: false,
      save: 'Сохранить',
    };
  },
  created() {
    this.save = 'Сохранить';
  },
  methods: {

   submit() {

   
      const token = localStorage.getItem('token');

      const config = {
        headers: { 'Authorization': `Bearer ${token}` }
      };

      const obj = {
        type: 'change',
        new_password: this.password,
        old_password: this.password_old,
      };

      if (this.password !== this.password_c) {
          this.$modal.show('dialog', {
          text: 'Пароли не совподают.',
          buttons: [
            {
              title: 'Ок'
            }
          ]});
        return;
      }

      this.$http.put('/password', obj, config)
        .then(res => {
          this.onSubmit = !this.onSubmit;

          this.password_old =  '',
          this.password = '',
          this.password_c = '',

            this.$modal.show('dialog', {
            text: 'Ваш пароль изменен!',
            buttons: [
              {
                title: 'Ок'
              }
            ]});
  
          setTimeout(()=>{
            this.onSubmit = !this.onSubmit;
            this.save = 'Изменить';
          }, 15000);
          this.save = 'Ок';
        })
        .catch(error => {
          if (error.response.data.error === 'Invalid password') {
            this.$modal.show('dialog', {
            text: 'Текущий пароль, неверный!',
            buttons: [
              {
                title: 'Ок'
              }
            ]});
          }
        });
    }
  },
}
</script>

