<template>
<nav>
  <img class="logo-user" src="../assets/otau_logo.png">
  <img class="menu" @click="mobOpenLeftNav" src="../assets/menu.svg" height="25">
  <ul>
    <li class="notif">
      <div @click="msgOpen" class="notif__msgOpen">
        <img  src="../assets/notification.png" class="notif__notif" alt="notif">
        <span v-show="notif" class="notif__call">{{ notif }}</span>
      </div>

      <ul v-show="showMsg" class="notif__msg-box scrollbar">
        <li
          @mouseover="mouseOver(item.id)"
          @mouseout="mouseOut(item.id)"
          v-for="(item, i) in itemsNews" :key="i"
        >
          <!-- <span class="notif__msg-box__rad">{{ item.name }}</span> -->
          <span class="notif__msg-box__text">{{ item.title }}</span>
          <span class="notif__msg-box__delete"
            v-show="item.showDelete"
            @click="deleteNews(item.id)"
          ></span>
        </li>
      </ul>
    </li>
    <li @click="getAllServices()" class="apps-open">
      <img src="./images/apps.svg" class="user-nav__apps" alt="apps">
    <div v-if="showApps" class="apps">
      <div class="item">
        <img src="../assets/ACCOUNT.png" alt="Account">
        <a href="#" class="loggout__item">Account</a>
      </div>
      <div class="item">
        <img src="../assets/BOARD.png" alt="Board">
        <a href="https://board.otau.org/" class="loggout__item">Board</a>
      </div>
      <div class="item">
        <img src="../assets/MESSENGER.png" alt="Messenger">
        <a href="https://msg.otau.org/" class="loggout__item">Messenger</a>
      </div>
      <div class="item">
        <img src="../assets/NETWORK.png" alt="Network">
        <a href="#" class="loggout__item">Network</a>
      </div>
  </div>
    </li>
    <li @click="loggout()" class="user-nav__user">
        <span class="user-nav__user-name">{{ userName }}</span>
        <div v-if="avatar" class="user-nav__box-avatar">
          <img class="user-nav__user-photo" v-bind:src="avatar" alt="Photo">
        </div>
        <div v-else>
          <span class="user-nav__user-photo__not">{{ userName[0] }}</span>
        </div>
        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
          <path fill="#666" fill-rule="evenodd" d="M16.297 9.5l-4.304 4.13-.459-.44-3.82-3.667-.714.684L11.993 15 17 10.195l-.703-.695z"/>
        </svg>

        <div v-if="showLoggout" class="loggout">
          <a v-if="(balance === null) ? false : true" href="#" class="loggout__item loggout__item-bb">Баланс Network coin: {{ (balance) ? balance : 0 }}</a>
          <a href="#" class="loggout__item" @click="clearLocalStor()">Выйти</a>
        </div>
    </li>
  </ul>
</nav>
</template>


<script>


const socket = io('https://otau.org/ws');

export default {
  name: 'UserPageHeader',
  props: {
    userName: String,
    avatar: String,
    token: String,
    balance: String | Number,
  },
  data() {
    return {
      showMsg: false,
      showApps: false,
      showLoggout: false,
      notif: 0,
      notifClicked: false,
      itemsNews: [],
      leftNAvOpen: false,
    };
  },
  mounted() {
    
    const config = {
      headers: { 'Authorization': `Bearer ${this.token}` }
    };
    this.$http.get('https://otau.org/notifications', config)
    .then(res => {
      
      let data = res.data;

      data.forEach(element => {
        element.showDelete = false;
      });

      this.itemsNews = data;

      if (!this.notifClicked) {
        this.notif = data.length;
      }

    })
    .catch(error => {
      console.log('Ошибка: ', error);
    });

    socket.on(this.token, (data) => {
      data.showDelete = false;
      this.itemsNews.unshift(data);
      if(!this.showMsg) {
        this.notif = this.notif += 1;
      }
    });

  },



  methods: {
    mobOpenLeftNav() {
      console.log("clicked");
      this.leftNAvOpen = !this.leftNAvOpen;
    },
    deleteNews(id) {
    const items = this.itemsNews.filter(item => id !== item.id);

    this.itemsNews = items;


    const config = {
      headers: { 'Authorization': `Bearer ${this.token}` }
    };


    this.$http.delete('https://otau.org/notifications/'+ id, config)
    .then(res => {
      console.log("deleted")
    })
    .catch(error => {
      console.log('Ошибка: ', error);
    });

    },
    mouseOver(id) {
      this.itemsNews.find(item => id === item.id).showDelete = true;
      
    },
    mouseOut(id) {
      this.itemsNews.find(item => id === item.id).showDelete = false;
    },
    clearLocalStor() {
      localStorage.removeItem('token');
      this.$emit('logged');
      this.$router.push('/');
    },
    getAllServices() {
      this.showLoggout = false;
      this.showMsg = false;
      this.showApps = !this.showApps;
      // this.$router.push('/service');
    },
    loggout() {
      this.showApps = false;
      this.showMsg = false;
      this.showLoggout = !this.showLoggout;
    },
    msgOpen() {

      this.notifClicked = true;

      if (this.notif === 0) {
        this.showApps = false;
        this.showLoggout = false;
        this.showMsg = false;
      } else {
        this.notif = 0;
        this.showApps = false;
        this.showLoggout = false;
        this.showMsg = !this.showMsg;
      }
    },
  },
};
</script>


