<template>
  <div id="app" :style="isLoggedStyle">
    <header v-if="isLogged">
      <UserPageHeader @logged="isLoggedChange" :userName="userName" :avatar="avatar" :token="token" :balance="balance"/>
    </header>
    <header v-else>
      <HomeHeader />
    </header>
    <main>
      <v-dialog name="login" :width="250" :height="200" />
      <router-view @logged="isLoggedChange" @setUserNameforHeader="setName" @setUserName="setNameName"/>
    </main>
  </div>
</template>

<script>
import HomeHeader from './home/HomeHeader.vue';
import UserPageHeader from './user/UserPageHeader.vue';

export default {
  name: 'app',
  components: {
    HomeHeader,
    UserPageHeader,
  },
  data() {
    return {
      isLogged: false,
      token: localStorage.getItem('token'),
      userName: '',
      avatar: '',
      balance: '',
    };
  },
  methods: {
    isLoggedChange() {
      this.isLogged = !this.isLogged;
    },
    setName(data){
      if (data.name === 'A') {
        this.avatar = data.avatar;
        return;
      }
      this.userName = data.name;
      this.avatar = data.avatar;
      this.balance = data.balance;
    },

    setNameName(name) {
      this.userName = name;
    },

    fetchIt() {
      if (this.token) {
        this.isLogged = !this.isLogged;
        this.$router.push('/user');
      } else {


        if (this.$route.path === '/register') {
          const inviter = this.$route.query.inviter_id;
          this.$router.push({ path: 'register', query: { inviter_id: inviter }});
        } else {
          this.$router.push('/');
        }
      }
    },
  },
  created() {
    this.fetchIt();
  },
  computed: {
    isLoggedStyle() {
      return {
        background: this.isLogged ? 'white' : '',
      };
    },
  },
};
</script>

