<template>
  <div class="center home">
    <div class="home__slogan">
      <h1 class="home__slogan-text">Eдиный аккаунт для всех сервисов otau</h1>
    </div>
    <div class="home__started">
      <img src="../assets/otau_logo.png" class="logo">
        <form v-if="stepForms === 1" @submit.prevent="restorePass">
          <div v-if="!preloader.isStepForm1">
            <div class="form-group">
              <label for="phone">
                <img src="../assets/phone.png" class="form__phone-icon">
              </label>
              <input
                id="phone"
                type="text"
                v-model="phone"
                @input="onInput"
                autocomplete="off"
                placeholder="Номер телефона"
                class="form__input"
                maxlength="15"
              >
            </div>
            <span class="valid-phone">{{ phoneValidate }}</span>
            <button type="submit">Далее</button>
            <a @click="toLogin">Отмена</a>
          </div>
          <div v-else>
            <div class="loader"></div>
          </div>
        </form>
        <form v-if="stepForms === 2" @submit.prevent="restorePassSms">
          <div v-if="!preloader.isStepForm2">
            <div class="form-group">
              <label for="sms">
                <img src="../assets/sms_code.png" class="form__phone-icon">
              </label>
              <input
                id="sms"
                type="number"
                v-model="smsCode"
                @input="onInputSms"
                autocomplete="off"
                placeholder="Подтверждение"
                class="form__input"
                maxlength="5"
              >
            </div>
            <span class="valid-phone">{{ smsValidate }}</span>
            <button type="submit">Далее</button>
            <a @click="toLogin">Отмена</a>
          </div>
          <div v-else>
            <div class="loader"></div>
          </div>
        </form>
        <form v-if="stepForms === 3" @submit.prevent="restorePassNewPass">
          <div v-if="!preloader.isStepForm3">
            <div class="form-group">
              <label for="password">
                <img src="../assets/password.png" class="form__phone-icon">
              </label>
              <input
                id="password"
                type="password"
                v-model="newPass"
                @input="onInputPass1"
                autocomplete="off"
                placeholder="Новый пароль"
                class="form__input"
                maxlength="27"
              >
            </div>
            <div class="form-group">
              <label for="conf-password">
                <img src="../assets/password.png" class="form__phone-icon">
              </label>
              <input
                id="conf-password"
                type="password"
                v-model="confNewPass"
                @input="onInputPass2"
                autocomplete="off"
                placeholder="Повторите пароль"
                class="form__input"
                maxlength="27"
              >
            </div>
            <span class="valid-phone">{{ passValidate }}</span>
            <button type="submit">Сохранить</button>
          </div>
          <div v-else>
            <div class="loader"></div>
          </div>
        </form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'RestorePassPage',
  data() {
    return {
      phone: '',
      smsCode: '',
      newPass: '',
      confNewPass: '',
      stepForms: 1,
      phoneValidate: '',
      smsValidate: '',
      passValidate: '',
      token: '',
      preloader: {
        isStepForm1: false,
        isStepForm2: false,
        isStepForm3: false,
      }
    };
  },
  methods: {
    restorePass() {
      if(isNaN(Number(this.phone))){
        this.phoneValidate = 'Введите номер телефона!';
        this.phone = '+';
      } else {

        this.preloader.isStepForm1 = true;

        this.$http.post('/password/forgot', { phone: this.phone }).then((data) => {
          this.preloader.isStepForm1 = false;
          this.stepForms = 2;
        }).catch((err) => {
          this.preloader.isStepForm1 = false;
          if (err.response.data.error === 'Send full phone number with +7') {
            this.phoneValidate = 'Введите полный номер телефона!';
          } else if (err.response.data.error === 'Phone not exist') {
            this.phoneValidate = 'Номер не зарегистрирован!';
          }
        });
      }

    },
    onInput(e) {
      this.phoneValidate = '';
      if (this.phone[0] !== '+') {
        this.phone = '+' + this.phone;
      }
      if (this.phone.length < 2 && e.inputType === 'deleteContentBackward') {
        this.phone = '';
      }
    },
    toLogin() {
      this.$router.push('/');
    },
    /*============== SMS ==================*/
    restorePassSms() {
      if(isNaN(Number(this.smsCode))){
        this.smsValidate = 'Введите валидный смс-код';
        this.smsCode = '';
      } else {
        if (this.smsCode.length === 5) {
          this.preloader.isStepForm2 = true;
          this.$http.post('/password/forgot/verify-phone', { phone: this.phone, code: this.smsCode }).then((res) => {
            this.preloader.isStepForm2 = false;
            this.stepForms = 3;
            this.token = res.data.access_token;
          }).catch((err) => {
            this.preloader.isStepForm2 = false;
            if (err.response.data.error === 'Code error') {
              this.smsValidate = 'Неверный код-СМС';
            }
          });
        }
      }
    },
    onInputSms(e) {
      this.smsValidate = '';
    },
    /*============== Passwords ==================*/
    restorePassNewPass() {
      if (this.newPass === this.confNewPass) {
        if (this.newPass.length < 6) {
          // error 2
          this.passValidate = 'Введите минимум 8-символов!';
        } else {

          this.preloader.isStepForm3 = true;

          const config = {
            headers: { 'Authorization': `Bearer ${this.token}` }
          };

          const obj = {
            new_password: this.newPass,
            type: 'forgot',
          };


          this.$http.put('/password', obj, config)
          .then(res => {

            this.preloader.isStepForm3 = false;
            this.$modal.show('dialog', {
            text: 'Пароль изменен!',
            buttons: [
              {
                title: 'Ок'
              }
            ]});


            this.$router.push('/');


            this.phone = '';
            this.smsCode = '';
            this.newPass = '';
            this.confNewPass = '';

          })
          .catch(error => {
            if (error.response.data.error === 'The new_password must consist of Latin letters, numbers and symbols') {
              this.passValidate = 'Введите пароль на латинице';
            }
          });

        }
      } else {
        // error 1
        this.passValidate = 'Пароли не совпадают!';
      }
    },
    onInputPass1() {
      this.passValidate = '';
    },
    onInputPass2() {
      this.passValidate = '';
    }
  },
}
</script>


