<template>
      <div v-if="loading" class="user">
          <div class="loader"></div>
      </div>
      <div v-else class="container">
        <div class="user-info">
          <ul class="user-info__photo">
            <li v-if="user.avatar" class="user-info__box-avatar">
              <img class="user-info__avatar loader-avatar" v-if="imgLoad">
              <img class="user-info__avatar" v-else v-bind:src="user.avatar">
            </li>
            <li v-else>
              <span class="radius loader-avatar" v-if="imgLoad"></span>
              <span class="radius" v-else>{{ user.name[0] }}</span>
            </li>

            <input type="file" @change="onFileSelected" id="file" name="file" class="inputfile">
            <label for="file">{{ text.avatarChange }}</label>

          </ul>
          <div class="info a-ml">
            <h3 class="info__username">{{ user.name }} {{ user.lastname }}</h3>
            <div class="info__row">
              <div class="info__left">
                <p v-show="user.nickname">Псевдоним:</p>
                <p>День рождения:</p>
                <p>Пол:</p>
                <p>Номер телефона:</p>
                <p v-show="user.email">Email:</p>
                <p v-show="user.address">Адрес:</p>
                <p v-show="user.postcode">Индекс:</p>
                <p v-show="user.region">Область:</p>
                <p v-show="user.city">Город:</p>
                <p v-show="user.country">Страна:</p>
              </div>
              <div class="info__right">
                <p
                  v-show="user.nickname"
                  @click="changeInfo"
                  class="info__change">{{ user.nickname }}</p>
                <p
                  v-show="user.birthday"
                  @click="changeInfo"
                  class="info__change"
                  >{{ user.birthday }}</p>
                <p>{{ user.gender   }}</p>
                <p class="height_phone">+{{ user.phone    }}</p>
                <p 
                  v-show="user.email" 
                  @click="changeInfo"
                  class="info__change">{{ user.email   }}</p>
                <p 
                  v-show="user.address" 
                  @click="changeInfo"
                  class="info__change">{{ user.address }}</p>
                <p 
                  v-show="user.postcode" 
                  @click="changeInfo"
                  class="info__change">{{ user.postcode }}</p>
                <p 
                  v-show="user.region" 
                  @click="changeInfo"
                  class="info__change">{{ user.region }}</p>
                <p 
                  v-show="user.city" 
                  @click="changeInfo"
                  class="info__change">{{ user.city }}</p>
                <p 
                  v-show="user.country" 
                  @click="changeInfo"
                  class="info__change">{{ user.country }}</p>
              </div>
            </div>
          </div>
        </div>
        <div class="user-images scrollbar">
          <div
            v-for="image in images"
            @mouseover="mouseOver(image.id)"
            @mouseout="mouseOut(image.id)"
            v-bind:key="image.id"
            class="user-images__box">
            <span class="user-images__delete"
              v-show="image.isShow"
              @click="deletedImage(image.id)">
            </span>
            <img :src="image.link">
          </div>
        </div>
      </div>
</template>

<script>
export default {
  name: 'Account',
  props: {
    user: Object,
  },
  data() {
    return {
      loading: false,
      showFullInfo: false,
      selectedFile: null,
      images: [],
      imgLoad: false,
      text: {
        avatarChange: 'Изменить',
      },
      token: localStorage.getItem('token'),
    };
  },
  created() {
    this.$http.get(`/user/data/avatar?access_token=${this.token}`)
    .then(res => {
      const data = res.data;
      data.map(item => item.isShow = false);
      this.images = data;
    })
    .catch(error => {
      console.log('Ошибка ' + error.response.data.error);
    });

    //this.user.birthday.slice(5, 7));

  },
  methods: {

    mouseOver(id) {
      this.images.find(item => id === item.id).isShow = true;
    },
    mouseOut(id) {
      this.images.find(item => id === item.id).isShow = false;
    },

    changeInfo(info) {
      this.$router.push('/user/profile');
      // this.notify();
    },

    deletedImage(id) {

      const obj = {
        photo_id: id,
      };

      const config = {
        headers: { 'Authorization': `Bearer ${this.token}` },
        data: obj,
      };


        this.$http.delete('/user/data/avatar', config)
        .then(res => {
          const result = this.images.filter(data => data.id !== id);
          this.images = result; // send Avatar
          this.user.avatar = res.data.link;
          this.$emit('changeAvatar', res.data.link); //change to header img
        })
        .catch(error => {
          console.log('Ошибка ' + error.response.data.error);
        });
    },

    notifyMe(){
      if (!("Notification" in window)) {
        alert("Ваш браузер не поддерживает уведомления.");
      } else if (Notification.permission === 'granted') {
        this.notify();
      } else if (Notification.permission !== 'denied') {
        Notification.requestPermission(()=>{
          if (permission === 'granted') {
            this.notify();
          }
        });
      }
    },

    notify() {
      const notification = new Notification('OTAU Messenger', {
        icon: 'https://otau.org/style/img/messenger.png',
        body: 'Привет! Ваше уведомение доставленно!',
      });
    },

    onFileSelected(event) {
      this.selectedFile = event.target.files[0];
      this.text.avatarChange = this.selectedFile.name;
      this.onUpload();
    },

    onUpload() {
      const fd  = new FormData();

      this.imgLoad = true;

      fd.append('avatar', this.selectedFile, this.selectedFile.name);

  
      const config = {
        headers: {
          'Authorization': `Bearer ${this.token}`,
          'Content-Type': 'multipart/form-data'
        },
        onUploadProgress: ProgressEvent => {
          console.log("loaded " + Math.round(ProgressEvent.loaded / ProgressEvent.total * 100) + '%');
        },
      };

      let self = this;

      this.$http.post('/user/data/avatar', fd, config)
        .then(res => {
          this.imgLoad = false;
          self.user.avatar = res.data.link;
          this.$emit('changeAvatar', res.data.link); //change to header img
          this.text.avatarChange = 'Изменить';
          self.getAllFiles();
        })
        .catch(error => {
          console.log('Ошибка ' + error.response.data.error);
        });

    },

    getAllFiles() {
      this.$http.get(`/user/data/avatar?access_token=${this.token}`)
      .then(res => {
        const data = res.data;
        data.map(item => item.isShow = false);
        this.images = data;
      })
      .catch(error => {
        console.log('Ошибка ' + error.response.data.error);
      });
    },
  }
}
</script>

