<template>
  <div class="container">
    <H3>Изменить персональные данные</H3>
    <form @submit.prevent="submit">
      <div class="form-group">
        <label for="name">Имя</label>
        <input 
          v-model.trim="userEdit.name"
          id="name"
          type="text"
          autocomplete="off"
          maxlength="20"
          >
      </div>
      <div class="form-group">
        <label for="lastname">Фамилия</label>
        <input
          v-model.trim="userEdit.lastname"
          id="lastname"
          type="text"
          autocomplete="off"
          maxlength="40"
          >
      </div>
      <div class="form-group">
        <label for="nickname">Псевдоним</label>
        <input id="nickname"
          type="text"
          autocomplete="off"
          v-model="userEdit.nickname"
          maxlength="20">
      </div>
      <div class="form-group">
        <label for="date">Дата рождения:</label>
        <datepicker
          id="date"
          v-model="date.value"
          :disabledDates="date.disabledDates"
          :language="date.ru"
          input-class="inputDate2"
          calendar-class="inputCalen2"></datepicker>
      </div>
      <div class="form-group">
        <label for="email">Email</label>
        <input
          v-model.trim="userEdit.email"
          id="email"
          type="email"
          autocomplete="off"
          placeholder="you@mail.com"
          maxlength="40"
          >
      </div>

      <div class="form-group">
        <label for="address">Адрес</label>
        <input id="address"
          type="text"
          autocomplete="off"
          v-model.trim="userEdit.address"
          maxlength="50">
      </div>

      <div class="form-group">
        <label for="region">Выбрать область</label>
        <select id="region" v-model="regionSelected" @change="regionChange(regionSelected)">
          <option disabled value="">Выберите один из вариантов</option>
          <option v-for="item in regions" :key="item.id" :value="item.id">{{ item.region }}</option>
        </select>
      </div>
      <div class="form-group">
        <label for="cities">Выбрать город</label>
        <select id="cities" v-model="citiesSelected">
          <option disabled value="">Выберите один из вариантов</option>
          <option v-for="item in cities" :key="item.id" :value="item.id">{{ item.city }}</option>
        </select>
      </div>

      <div class="form-group">
        <label for="postcode">Индекс</label>
        <input id="postcode"
          type="text"
          autocomplete="off"
          v-model="userEdit.postcode"
          maxlength="6">
      </div>
      <button type="submit" :disabled="onSubmit">{{ save }}</button>
    </form>
  </div>
</template>

<script>
import Datepicker from 'vuejs-datepicker';
import { ru } from 'vuejs-datepicker/dist/locale';

export default {
  name: 'Profile',
  components: {
    Datepicker,
  },
  props: {
    user: Object,
  },
  data() {
    return {
      regions: [],
      cities: [],
      regionSelected: '',
      citiesSelected: '',
      onSubmit: false,
      save: 'Изменить данные',
      userEdit: {
        name: '',
        lastname: '',
        email: '',
        address: '',
        postcode: '',
        nickname: '',
        region_id: '',
        city_id: '',
      },
      date: {
        value: '',
        ru: ru,
        disabledDates: {
          to: new Date(1902, 1, 1), // Disable all dates up to specific date
          from: new Date(2003, 0, 0),
        },
      },
    };
  },
  created() {
    this.date.value = this.user.birthday;


    //set default
    this.userEdit.name = this.user.name;
    this.userEdit.lastname = this.user.lastname;
    this.userEdit.email = this.user.email;
    this.userEdit.address = this.user.address;
    this.userEdit.postcode = this.user.postcode;
    this.userEdit.nickname = this.user.nickname;



    this.save = 'Изменить данные';

    this.$http.get('/regions?country_id=68')
    .then(res => {
      // const data = res.data;
      // data.map(item => item.selected = false);
      this.regions = res.data;
    })
    .catch(error => {
      console.log('Ошибка ' + error.response.data.error);
    });

  },
  methods: {

    regionChange(region) {
      this.$http.get('/cities?region_id=' + region)
      .then(res => {
        this.cities = res.data;
      })
      .catch(error => {
        console.log('Ошибка ' + error.response.data.error);
      });
    },

    // citiesChange(city) {
    // },

   submit() {

      const obj = {};

      if (this.userEdit.name) {
        obj.name = this.userEdit.name;
        this.user.name = this.userEdit.name;
      }

      if (this.userEdit.lastname) {
        obj.lastname = this.userEdit.lastname;
        this.user.lastname = this.userEdit.lastname;
      }

      if (this.regionSelected) {
        obj.region_id = this.regionSelected;
        const region = this.regions.find(region => region.id === this.regionSelected).region;
        this.user.region = region;
      }

      if (this.citiesSelected) {
        obj.city_id = this.citiesSelected;
        const city = this.cities.find(city => city.id === this.citiesSelected).city;
        this.user.city = city;
      }

      if (this.userEdit.email) {
        obj.email = this.userEdit.email;
        this.user.email = (this.userEdit.email === '') ? this.user.email : this.userEdit.email;
      }

      if (this.userEdit.address) {
        this.user.address = (this.userEdit.address === '') ? this.user.address : this.userEdit.address;
        obj.address = this.userEdit.address;
      }

      if (this.userEdit.postcode) {
        this.user.postcode = (this.userEdit.postcode === '') ? this.user.postcode : this.userEdit.postcode;
        obj.postcode = this.userEdit.postcode;
      }

      if (this.userEdit.nickname) {
        this.user.nickname = (this.userEdit.nickname === '') ? this.user.nickname : this.userEdit.nickname;
        obj.nickname = this.userEdit.nickname;
      }

      if (this.date.value !== this.user.birthday) {
        // date transform to server
        let d = this.date.value; 
        d = d.getFullYear() + "-" + (d.getMonth() + 1) + "-" + d.getDate();

        this.user.birthday = d;
        obj.birthday = d;
      }


      // this.regions.filter(region => region.id === this.regionSelected).selected = true;


      const token = localStorage.getItem('token');

      const config = {
        headers: { 'Authorization': `Bearer ${token}` }
      };



      this.$http.put('/user/data', obj, config)
        .then(res => {
          this.onSubmit = !this.onSubmit;

          if (obj.name) {
            this.$emit('changeName', obj.name); //change to header name
          }
          
          setTimeout(()=>{
            this.onSubmit = !this.onSubmit;
            this.save = 'Изменить данные';
          }, 15000);
          this.save = 'Ок';
        })
        .catch(error => {
          console.log('Ошибка ' + error.response.data.error);
        });
    }
  },
}
</script>

