import Vue from 'vue';
import Router from 'vue-router';

import HomePage from '../home/HomePage.vue';
import UserPage from '../user/UserPage.vue';
import ServicePage from '../service/ServicePage.vue';
import RegisterPage from '../register/RegisterPage.vue';
import RestorePassPage from '../restorePass/RestorePassPage.vue';

import Account from '../user/account/Account.vue';
import Profile from '../user/profile/Profile.vue';
import Balance from '../user/balance/Balance.vue';
import Settings from '../user/settings/Settings.vue';
import Faq from '../user/faq/Faq.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [{
    path: '/',
    name: 'Home',
    component: HomePage,
  }, {
    path: '/user',
    component: UserPage,
    children: [
      {
        path: '',
        component: Account,
      },
      {
        path: 'account',
        component: Account,
      },
      {
        path: 'profile',
        component: Profile,
      },
      {
        path: 'balance',
        component: Balance,
      },
      {
        path: 'settings',
        component: Settings,
      },
      {
        path: 'faq',
        component: Faq,
      },
    ],
  }, {
    path: '/service',
    name: 'Service',
    component: ServicePage,
  }, {
    path: '/register',
    name: 'Register',
    component: RegisterPage,
  }, {
    path: '/restore-password',
    name: 'RestorePass',
    component: RestorePassPage,
  }],
});
